import React,{Fragment} from 'react';
import Header from "../components/Header_indust";
import PageAbout from "../components/About/page";
import Services from "../components/Services_indust";
import BrandLogo from "../components/BrandLogo";
import CallToAction from "../components/CallToAction";
import Footer from "../components/Footer";
import MobileMenu from "../components/MobileMenu_indust";

import ServiceThumb from '../assets/img/about.jpg'

const PageService = () => {
    return (
        <Fragment>
            <Header/>
        
            <PageAbout
                title={'Our Services'}
                heading="Provide best <br/> Industrial Solutions"
                thumb={ServiceThumb}
                content= "<strong>YAS</strong> electric company is specialized in maintaining generators, ECMs and other electronic devices that are related to construction sites.."
            />
            <Services/>
            <BrandLogo/>
            <CallToAction/>
            <Footer/>
            <MobileMenu/>
        </Fragment>
    );
};

export default PageService;