import React from 'react';
import {BrowserRouter as Router, Switch, Route} from "react-router-dom";

/*
* @ All pages Import
*/

import Home from './pages/Home'
import IndustrialPage from './pages/IndustrialPage'
import MedicalPage from './pages/MedicalPage'
import Service_indust from './pages/Service_indust'
import ServiceDetails from "./pages/ServiceDetails";
import ServiceDetails_indust from "./pages/ServiceDetails_indust";

import BlogGridRightSidebar from './pages/BlogGridRightSidebar';
import BlogGridLeftSidebar from './pages/BlogGridLeftSidebar';
import BlogGridWithoutSidebar from './pages/BlogGridWithoutSidebar';
import BlogListLeftSidebar from './pages/BlogListLeftSidebar';
import BlogListRightSidebar from './pages/BlogListRightSidebar';
import BlogDetailsPage from "./pages/BlogDetails";
import Team from "./pages/Team";

import PageECM from "./pages/ECM";
import ECMDetails from "./pages/ECMDetails";
import ECMDetails_CAT from "./pages/ECMDetails_CAT";
import ECMDetails_PERK from "./pages/ECMDetails_PERK";
import ECMDetails_VOLV from "./pages/ECMDetails_VOLV";
import ECMDetails_CUMM from "./pages/ECMDetails_CUMM";
import ECMDetails_DD from "./pages/ECMDetails_DD";

import DISPDetails_CAT from "./pages/DISPDetails_CAT"
import DISPDetails_DSE from "./pages/DISPDetails_DSE"
import DISPDetails_PowWiz from "./pages/DISPDetails_PowWiz"
import DISPDetails_VOLV from "./pages/DISPDetails_VOLV"

import JoyStickDetails_CAT from "./pages/JoyStickDetails_CAT"

import Clients_indust from "./pages/Clients_indust";
import TeamDetails from "./pages/TeamDetails";
import About from "./pages/About";
import Contact from "./pages/Contact";
import Error404 from "./pages/Error404";
import ScrollToTop from "./helpers/ScrollToTop";
import Soon from "./pages/Soon";


const App = () => {
    return (
        <Router>
            <ScrollToTop>
                <Switch>
                    <Route exact path={`${process.env.PUBLIC_URL + '/'}`} component={Home}/>
                    <Route exact path={`${process.env.PUBLIC_URL + '/industrial'}`} component={IndustrialPage}/>
                    <Route exact path={`${process.env.PUBLIC_URL + '/medical'}`} component={Soon}/>
                    <Route exact path={`${process.env.PUBLIC_URL + '/services_indust'}`} component={Service_indust}/>
                    <Route path={`${process.env.PUBLIC_URL + '/service/:serviceID'}`} component={ServiceDetails}/>
                    <Route path={`${process.env.PUBLIC_URL + '/service_indust/:serviceID'}`} component={ServiceDetails_indust}/>

                    <Route exact path={`${process.env.PUBLIC_URL + "/blog-grid-right-sidebar"}`}
                           component={BlogGridRightSidebar}/>
                    <Route exact path={`${process.env.PUBLIC_URL + "/blog-grid-left-sidebar"}`}
                           component={BlogGridLeftSidebar}/>
                    <Route exact path={`${process.env.PUBLIC_URL + "/blog-grid-without-sidebar"}`}
                           component={BlogGridWithoutSidebar}/>
                    <Route exact path={`${process.env.PUBLIC_URL + "/blog-list-left-sidebar"}`}
                           component={BlogListLeftSidebar}/>
                    <Route exact path={`${process.env.PUBLIC_URL + "/blog-list-right-sidebar"}`}
                           component={BlogListRightSidebar}/>
                    <Route path={`${process.env.PUBLIC_URL + "/blog/:blogID"}`} component={BlogDetailsPage}/>
                    <Route exact path={`${process.env.PUBLIC_URL + "/team"}`} component={Team}/>
                    <Route exact path={`${process.env.PUBLIC_URL + "/Clients_indust"}`} component={Clients_indust}/>
                    <Route path={`${process.env.PUBLIC_URL + "/Client/:teamID"}`} component={TeamDetails}/>

                    <Route path={`${process.env.PUBLIC_URL + "/ECM/:ECMID"}`} component={ECMDetails}/>
                    <Route path={`${process.env.PUBLIC_URL + "/ECM_CAT/:ECMID"}`} component={ECMDetails_CAT}/>
                    <Route path={`${process.env.PUBLIC_URL + "/ECM_PERK/:ECMID"}`} component={ECMDetails_PERK}/>
                    <Route path={`${process.env.PUBLIC_URL + "/ECM_VOLV/:ECMID"}`} component={ECMDetails_VOLV}/>
                    <Route path={`${process.env.PUBLIC_URL + "/ECM_CUMM/:ECMID"}`} component={ECMDetails_CUMM}/>
                    <Route path={`${process.env.PUBLIC_URL + "/ECM_DD/:ECMID"}`} component={ECMDetails_DD}/>

                    <Route path={`${process.env.PUBLIC_URL + "/DISP_CAT/:ECMID"}`} component={DISPDetails_CAT}/>
                    <Route path={`${process.env.PUBLIC_URL + "/DISP_PowWiz/:ECMID"}`} component={DISPDetails_PowWiz}/>
                    <Route path={`${process.env.PUBLIC_URL + "/DISP_DSE/:ECMID"}`} component={DISPDetails_DSE}/>
                    <Route path={`${process.env.PUBLIC_URL + "/DISP_VOLV/:ECMID"}`} component={DISPDetails_VOLV}/>

                    <Route path={`${process.env.PUBLIC_URL + "/JoyStick/:ECMID"}`} component={JoyStickDetails_CAT}/>

                    <Route exact path={`${process.env.PUBLIC_URL + "/about"}`} component={About}/>
                    <Route exact path={`${process.env.PUBLIC_URL + "/contact"}`} component={Contact}/>
                    <Route exact component={Error404}/>
                </Switch>
            </ScrollToTop>
        </Router>
    );
};

export default App;