import React from 'react';
import teamData from '../../data/ECM/PERK.json';
import TeamMember from "../../components/ECM/Member_PERK";
import CAT_LOGO from "../../assets/img/service/PERK.jpg"

import BlankSpace from "../../components/BlankSpace";

const TeamPage = () => {
    return (
        <div className="team-page-area-wrapper bg-offwhite sp-y sm-top">
                <img src={CAT_LOGO}  align="center"/>
                <h1> </h1>
                <h1> </h1>
                <div className="row mtn-30">
                    {
                        teamData.map(member=>(
                            <TeamMember key={member.id} type={'page'} id={member.id} name={member.name} designation={member.designation} profilePic={member.profilePic}/>
                        ))
                    }
                </div>
        </div>
    );
};

export default TeamPage;