import React, {Component, Fragment} from 'react';
import {Link} from "react-router-dom";
import socialNetworks from "../../data/SocialNetworks/socials";

// Images Import
import logosmall from '../../assets/img/logosmall.png'
import logobig from '../../assets/img/logobig.png'
import home1 from '../../assets/img/home/ind.jpg'
import home2 from '../../assets/img/home/med.jpg'


class HomePage extends Component {

    componentDidMount() {
        window.addEventListener('scroll', this.isSticky);
    }

    componentWillUnmount() {
        window.removeEventListener('scroll', this.isSticky);
    }

    isSticky = (e) => {
        const header = document.querySelector('.header-section');
        const scrollTop = window.scrollY;
        scrollTop >= 250 ? header.classList.add('sticky') : header.classList.remove('sticky');
    };

    render() {
        return (
            <Fragment>
                <div className="header-section section" >                         
                                    <div className="hero-content text-center">
                                        <img className={'logo-light'} src={logobig} alt="logo" />
                                        <h1  style={{ color: 'black'}}>ELECTRIC COMPANY </h1>
                                    </div>
                </div>
                
                    <div className="container">

                        <div className="row ">
                            <div className="col-lg-6 col-md-6">
                                <div className="demo-item">
                                    <Link to={`${process.env.PUBLIC_URL + "/industrial"}`} className="image">
                                        <img src={home1}/>
                                        <i className="fa fa-long-arrow-right"/>
                                    </Link>
                                    <h2 className="title">
                                        <a href={`${process.env.PUBLIC_URL + "/industrial"}`}  >Industrial</a>
                                    </h2>
                                </div>
                            </div>

                            <div className="col-lg-6 col-md-6">
                                <div className="demo-item">
                                    <Link to={`${process.env.PUBLIC_URL + "/medical"}`} className="image" >
                                        <img src={home2}/>
                                        <i className="fa fa-long-arrow-right"/>
                                    </Link>
                                    <h4 className="title">
                                        <a href={`${process.env.PUBLIC_URL + "/medical"}`}>Medical</a>
                                    </h4>
                                </div>
                            </div>
                            
                        </div>

                        <div className="off-canvas-item">
                <div className="social-icons">
                    {
                        socialNetworks.map(social=>(
                            <a key={social.id} href={`https://${social.networkName}.com/${social.username}`} target="_blank" rel="noopener noreferrer">
                                <i className={`fa fa-${social.networkName}`}/>
                            </a>
                        ))
                    }
                </div>
                <div className="copyright-content">
                    <p>  YAS electric {new Date().getFullYear()} All Right Reserved.</p>
                </div>
            </div>

                    </div>

           

                
            </Fragment>
        );
    }
}

export default HomePage;