import React,{Fragment} from 'react';
import PageWrapper from "../../components/PageWrapper";
import RelatedServices from "../../components/RelatedServices";

import ServicesData from "../../data/Services/services_indust.json";
import ECM_CAT from "../ECM_CAT"
import ECM_CUMM from "../ECM_CUMM"
import ECM_DD from "../ECM_DD"
import ECM_PERK from "../ECM_PERK"
import ECM_VOLV from "../ECM_VOLV"

import DISP_CAT from "../DISP_CAT"
import DISP_DSE from "../DISP_DSE"
import DISP_PowWiz from "../DISP_PowWiz"
import DISP_VOLV from "../DISP_VOLV"

import JoyStick_CAT from "../JoyStick_CAT"



const ServiceDetails = () => {
    const serviceID = new URLSearchParams(window.location.search).get("id");

    return (
        <Fragment>
            <PageWrapper classes="sm-top service-details-wrapper">
                <div>
                   {{ 1: <>
                         <ECM_CAT/> 
                         <ECM_PERK/> 
                         <ECM_VOLV/> 
                         <ECM_CUMM/> 
                         <ECM_DD/> 
                         </>,
                      3: <>
                         <JoyStick_CAT/> 
                         </>,
                      2: <>
                         <DISP_CAT/> 
                         <DISP_PowWiz/> 
                         <DISP_DSE/> 
                         <DISP_VOLV/> 
                         </>
                    }[serviceID ]}
                </div>    
            </PageWrapper>

            <RelatedServices/>
        </Fragment>
    );
};

export default ServiceDetails;