import React,{Fragment} from 'react';
import Header from "../components/Header_indust";
import ServiceDetails from "../templates/ServiceDetails_indust";
import CallToAction from "../components/CallToAction";
import Footer from "../components/Footer";
import MobileMenu from "../components/MobileMenu_indust";
import BrandLogo from "../components/BrandLogo";



const PageServiceDetails_indust = () => {
    return (
        <Fragment>
            <Header/>
            <ServiceDetails/>
            <BrandLogo/>
            <CallToAction/>
            <Footer/>
            <MobileMenu/>
        </Fragment>
    );
};

export default PageServiceDetails_indust;