import React, {Fragment} from 'react';

import Header from '../components/Header_indust'
import Slider from '../components/Slider/IndustrialPage'

import Services from '../components/Services_indust'
import Team from "../components/Team/home-one";
import Blog from "../components/Blog";
import BrandLogo from "../components/BrandLogo";
import Funfact from "../components/Funfact";
import BlankSpace from "../components/BlankSpace";
import Footer from "../components/Footer";
import Testimonials from "../components/Testimonials/home-one";
import MobileMenu from "../components/MobileMenu_indust";
import PageAbout from "../components/About/page";

import ServiceThumb from '../assets/img/about_home_indust.jpg'

const IndustrialPage = () => {
    return (
        <Fragment>
            <Header/>
            <Slider/>
            
            <Services classes="sm-top-wt"/>
            <PageAbout
                heading="Provide best <br/> Industrial Solutions"
                thumb={ServiceThumb}
                content="<b>YAS</b>  electric company is specialized in maintaining generators, ECMs and other electronic devices that are related to construction sites.."
            />
            <BrandLogo/> 
            <BlankSpace/>
            <Footer/>
            <MobileMenu/>
        </Fragment>
    );
};

export default IndustrialPage;