import React from 'react';
import ContactInfo from "./ContactInfo";

const ContactPage = () => {
    return (
        <div className={'contact-page-area-wrapper sp-y'}>
            <div className="container">
                <div className="contact-content-wrap">
                    <div className="row">
                        <div className="col-lg-8">
                            <div className="contact-form-area contact-method">
                                <iframe
                                  src="https://www.google.com/maps/embed?pb=!1m18!1m12!1m3!1d1026.8562935855641!2d31.367514242650124!3d30.039178575250407!2m3!1f0!2f0!3f0!3m2!1i1024!2i768!4f13.1!3m3!1m2!1s0x14583c32e93954c7%3A0x546fa4c83bfc0004!2sCity%20Towers!5e0!3m2!1sen!2seg!4v1588861736258!5m2!1sen!2seg"
                                  width={800}
                                  height={800}
                                  frameBorder={0}
                                  style={{ border: 0 , maxWidth: "100%", maxheight: "100%"}}
                                  allowFullScreen
                                  aria-hidden="false"
                                  tabIndex={0}
                                />
                            </div>
                        </div>

                        <div className="col-lg-4">
                            <div className="contact-information contact-method">
                                <div className="contact-info-con">
                                    <h3>Contact Info</h3>
                                    <ContactInfo
                                        address="49 El Narges 1 , City Tower, <br/>Nasr City, Cairo, EGYPT <br/>https://yaselectric.com/ <br/>(+02) 0127 504 8001"
                                    />
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        </div>
    );
};

export default ContactPage;