import React from 'react';
import {Link} from "react-router-dom";
import error from '../assets/img/soon.png'

const Soon = () => {
    return (
        <div style={styles}>
        <div>
            <img src={error} />
            <Link to="/" className={'btn-brand'} style={{display: 'inline-Block', marginTop: 30}}>Go Back
                Home</Link>
        </div>
    </div>
    );
};

const styles = {
    maxWidth: 900,
    margin: 'auto',
    display: 'flex',
    justifyContent: 'center',
    alignItems: 'center',
    height: '100vh',
    textAlign: 'center',
    padding: 30
};

export default Soon;